<template>
	<view class="order-list">
		<order-item
			v-for="(order, index) in orders"
			:key="order.id"
			class="order-item-out"
			:order="order"
			@click.native="onClickOrder(index)"
		>
		</order-item>
	</view>
</template>

<script>
	import OrderItem from '@/components/order-item/index.vue'
	export default {
		name: 'OrderList',
		components: {
			OrderItem
		},
		props: {
			orders: {
				type: Array,
				default: () => ([])
			}
		},
		data() {
			return {
				
			}
		},
		methods: {
			onClickOrder(index) {
				const orderItem = this.orders[index]
				uni.navigateTo({
					url: `/pages/delivery-order-detail/index?orderId=${orderItem.id}`
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.order-list {
	}
	
	.order-item-out {
		display: block;
		
		& + .order-item-out {
			margin-top: 30upx;
		}
	}
</style>
